"use client";

import React, { useState, useEffect, useRef, useCallback } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  Play,
  Pause,
  RotateCcw,
  Coffee,
  Brain,
  Armchair,
  Timer,
  History,
  Maximize2,
  Minimize2,
  CheckCircle2,
  Square,
  Settings2,
  X,
} from "lucide-react";
import { usePomodoro } from "@/lib/db/usePomodoro";
import { format } from "date-fns";

// --- TYPES & CONFIG ---
type Mode = "work" | "short" | "long";

interface TimerSettings {
  work: number;
  short: number;
  long: number;
}

const MODE_CONFIG = {
  work: {
    label: "Deep Focus",
    color: "text-indigo-600",
    bg: "bg-indigo-500",
    gradient: "from-indigo-500 to-purple-600",
    icon: Brain,
  },
  short: {
    label: "Short Break",
    color: "text-teal-600",
    bg: "bg-teal-500",
    gradient: "from-teal-400 to-emerald-500",
    icon: Coffee,
  },
  long: {
    label: "Long Break",
    color: "text-blue-600",
    bg: "bg-blue-500",
    gradient: "from-blue-400 to-cyan-500",
    icon: Armchair,
  },
};

export default function PomodoroPage() {
  const { addSession, todaySessions } = usePomodoro();

  // --- STATE ---
  const [mode, setMode] = useState<Mode>("work");
  const [settings, setSettings] = useState<TimerSettings>({
    work: 25,
    short: 5,
    long: 15,
  });

  const [timeLeft, setTimeLeft] = useState(settings.work * 60);
  const [isActive, setIsActive] = useState(false);
  const [isZenMode, setIsZenMode] = useState(false);
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);

  const audioRef = useRef<HTMLAudioElement | null>(null);
  const timerInterval = useRef<NodeJS.Timeout | null>(null);
  const initialTimeRef = useRef(settings.work * 60);

  // --- INITIALIZATION ---
  useEffect(() => {
    audioRef.current = new Audio(
      "https://actions.google.com/sounds/v1/alarms/beep_short.ogg"
    );

    const savedSettings = localStorage.getItem("pomodoro_settings");
    let currentSettings = settings;

    if (savedSettings) {
      const parsed = JSON.parse(savedSettings);
      setSettings(parsed);
      currentSettings = parsed;
    }

    const savedTarget = localStorage.getItem("pomodoro_target_time");
    const savedMode = localStorage.getItem("pomodoro_mode") as Mode;

    if (savedTarget && savedMode) {
      const targetTime = parseInt(savedTarget, 10);
      const now = Date.now();
      const diff = Math.ceil((targetTime - now) / 1000);

      if (diff > 0) {
        setMode(savedMode);
        setTimeLeft(diff);
        setIsActive(true);
        initialTimeRef.current = currentSettings[savedMode] * 60;
      } else {
        localStorage.removeItem("pomodoro_target_time");
        localStorage.removeItem("pomodoro_mode");
        setMode(savedMode);
        setTimeLeft(0);
      }
    } else {
      setTimeLeft(currentSettings.work * 60);
      initialTimeRef.current = currentSettings.work * 60;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // --- TIMER LOOP ---
  useEffect(() => {
    if (isActive) {
      if (!localStorage.getItem("pomodoro_target_time")) {
        const targetTime = Date.now() + timeLeft * 1000;
        localStorage.setItem("pomodoro_target_time", targetTime.toString());
        localStorage.setItem("pomodoro_mode", mode);
      }

      timerInterval.current = setInterval(() => {
        const targetTime = parseInt(
          localStorage.getItem("pomodoro_target_time") || "0",
          10
        );
        const now = Date.now();
        const diff = Math.ceil((targetTime - now) / 1000);

        if (diff <= 0) {
          handleTimerComplete();
        } else {
          setTimeLeft(diff);
          document.title = `${formatTime(diff)} - ${MODE_CONFIG[mode].label}`;
        }
      }, 1000);
    } else {
      if (timerInterval.current) clearInterval(timerInterval.current);
      localStorage.removeItem("pomodoro_target_time");
      document.title = "TaskGlyph - Focus";
    }
    return () => {
      if (timerInterval.current) clearInterval(timerInterval.current);
    };
  }, [isActive, mode, timeLeft]);

  // --- LOGIC HANDLERS ---
  const handleTimerComplete = useCallback(() => {
    setIsActive(false);
    setTimeLeft(0);
    localStorage.removeItem("pomodoro_target_time");
    audioRef.current?.play().catch(() => {});

    const duration = settings[mode];
    addSession(duration, mode === "work" ? "work" : "break");

    setTimeout(() => {
      if (mode === "work") {
        if (confirm("Focus complete! Start break?")) switchMode("short");
      } else {
        if (confirm("Break over! Start focus?")) switchMode("work");
      }
    }, 100);
  }, [mode, settings, addSession]); // Removed switchMode from deps here to avoid cycle, defined below

  // Define switchMode separately to avoid circular dependency in useCallback
  const switchMode = (newMode: Mode) => {
    if (isActive) return;
    setMode(newMode);
    setTimeLeft(settings[newMode] * 60);
    initialTimeRef.current = settings[newMode] * 60;
    localStorage.setItem("pomodoro_mode", newMode);
    localStorage.removeItem("pomodoro_target_time");
  };

  const handleStopAndSave = () => {
    if (!isActive && timeLeft === initialTimeRef.current) return;
    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");

    const timeSpentSeconds = initialTimeRef.current - timeLeft;
    const timeSpentMinutes = Math.floor(timeSpentSeconds / 60);

    if (timeSpentMinutes >= 1) {
      if (confirm(`Save ${timeSpentMinutes} minutes session?`)) {
        addSession(timeSpentMinutes, mode === "work" ? "work" : "break");
      }
    }
    setTimeLeft(settings[mode] * 60);
  };

  const toggleTimer = () => {
    if (!isActive && timeLeft === 0) setTimeLeft(settings[mode] * 60);
    setIsActive(!isActive);
  };

  const resetTimer = () => {
    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");
    setTimeLeft(settings[mode] * 60);
  };

  const saveSettings = (newSettings: TimerSettings) => {
    setSettings(newSettings);
    localStorage.setItem("pomodoro_settings", JSON.stringify(newSettings));
    if (!isActive) {
      setTimeLeft(newSettings[mode] * 60);
      initialTimeRef.current = newSettings[mode] * 60;
    }
    setIsSettingsOpen(false);
  };

  const formatTime = (seconds: number) => {
    const m = Math.floor(seconds / 60);
    const s = seconds % 60;
    return `${m}:${s < 10 ? "0" : ""}${s}`;
  };

  // --- VISUALS ---
  const radius = 120;
  const circumference = 2 * Math.PI * radius;
  const totalTime = settings[mode] * 60;
  const progress =
    totalTime > 0 ? ((totalTime - timeLeft) / totalTime) * circumference : 0;

  const CurrentIcon = MODE_CONFIG[mode].icon;
  const sessionsCount =
    todaySessions?.filter((s) => s.type === "work").length || 0;
  const totalFocusMinutes =
    todaySessions
      ?.filter((s) => s.type === "work")
      .reduce((acc, curr) => acc + curr.durationMinutes, 0) || 0;

  // --- RENDER HELPER FOR TIMER CIRCLE ---
  const TimerCircle = ({
    sizeClass = "w-96 h-96",
    strokeWidth = 6,
    fontSizeClass = "text-8xl",
    isZen = false,
  }) => (
    <div className={`relative ${sizeClass} flex items-center justify-center`}>
      {/* Glow (Zen only or active) */}
      <div
        className={`absolute inset-0 rounded-full blur-[60px] opacity-30 bg-gradient-to-tr ${
          MODE_CONFIG[mode].gradient
        } transition-opacity duration-700 ${
          isActive ? "opacity-60" : "opacity-20"
        }`}
      ></div>

      <svg className="w-full h-full transform -rotate-90 drop-shadow-xl relative z-10">
        <circle
          cx="50%"
          cy="50%"
          r="45%" // Responsive radius
          stroke="currentColor"
          strokeWidth={strokeWidth}
          fill="transparent"
          className={isZen ? "text-white/10" : "text-gray-100"}
        />
        <circle
          cx="50%"
          cy="50%"
          r="45%"
          stroke="url(#gradient)"
          strokeWidth={strokeWidth}
          fill="transparent"
          strokeDasharray={circumference} // This is fixed based on calc, simpler SVG logic might be needed for true fluid resizing, but this works for fixed sizes
          // Note: For responsive SVG dasharray, we stick to fixed pixels for simplicity in logic
          strokeDashoffset={-progress}
          strokeLinecap="round"
          className="transition-all duration-1000 ease-linear"
        />
        <defs>
          <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop
              offset="0%"
              stopColor={
                mode === "work"
                  ? "#6366f1"
                  : mode === "short"
                  ? "#14b8a6"
                  : "#3b82f6"
              }
            />
            <stop
              offset="100%"
              stopColor={
                mode === "work"
                  ? "#a855f7"
                  : mode === "short"
                  ? "#10b981"
                  : "#06b6d4"
              }
            />
          </linearGradient>
        </defs>
      </svg>

      <div className="absolute inset-0 flex flex-col items-center justify-center z-20">
        <span
          className={`${fontSizeClass} font-bold tracking-tighter font-mono tabular-nums leading-none ${
            isZen ? "text-white" : "text-gray-900"
          }`}
        >
          {formatTime(timeLeft)}
        </span>
        <div
          className={`flex items-center gap-2 mt-4 px-3 py-1 rounded-full bg-opacity-10 ${
            isZen ? "bg-white/10 text-white/60" : "bg-gray-100 text-gray-500"
          }`}
        >
          <CurrentIcon size={14} />
          <span className="text-xs font-bold uppercase tracking-widest">
            {isActive ? "Focusing" : "Paused"}
          </span>
        </div>
      </div>
    </div>
  );

  // --- RENDER ZEN MODE (Clean Fullscreen Overlay) ---
  if (isZenMode) {
    return (
      <div className="fixed inset-0 z-[9999] bg-[#0a0a0a] flex flex-col items-center justify-center text-white">
        {/* Zen Exit Button */}
        <button
          onClick={() => setIsZenMode(false)}
          className="absolute top-8 right-8 p-4 rounded-full bg-white/5 hover:bg-white/10 text-white/50 hover:text-white transition-all"
        >
          <Minimize2 size={24} />
        </button>

        {/* Timer */}
        <div className="scale-110 sm:scale-125">
          <TimerCircle
            sizeClass="w-[500px] h-[500px]"
            strokeWidth={4}
            fontSizeClass="text-9xl"
            isZen={true}
          />
        </div>

        {/* Minimal Controls */}
        <div className="mt-16 flex items-center gap-12">
          <button
            onClick={resetTimer}
            className="text-white/30 hover:text-white transition-colors p-4"
          >
            <RotateCcw size={24} />
          </button>
          <button
            onClick={toggleTimer}
            className={`w-20 h-20 rounded-[2.5rem] flex items-center justify-center text-white shadow-2xl hover:scale-105 transition-all bg-gradient-to-tr ${MODE_CONFIG[mode].gradient}`}
          >
            {isActive ? (
              <Pause size={32} fill="currentColor" />
            ) : (
              <Play size={32} fill="currentColor" className="ml-1" />
            )}
          </button>
          <div className="w-10 flex justify-center">
            {/* Only show stop if started */}
            {timeLeft !== settings[mode] * 60 && (
              <button
                onClick={handleStopAndSave}
                className="text-red-400/50 hover:text-red-400 transition-colors p-4"
              >
                <Square size={24} fill="currentColor" />
              </button>
            )}
          </div>
        </div>
      </div>
    );
  }

  // --- RENDER NORMAL MODE ---
  return (
    <div className="h-screen w-full overflow-hidden bg-[#F3F4F6] flex items-center justify-center relative p-6">
      {/* Background Blobs */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div
          className={`absolute top-[-10%] left-[-10%] w-[600px] h-[600px] rounded-full mix-blend-multiply filter blur-[120px] opacity-30 animate-blob ${MODE_CONFIG[mode].bg}`}
        ></div>
        <div
          className={`absolute bottom-[-10%] right-[-10%] w-[600px] h-[600px] rounded-full mix-blend-multiply filter blur-[120px] opacity-30 animate-blob animation-delay-2000 ${MODE_CONFIG[mode].bg}`}
        ></div>
      </div>

      <div className="w-full max-w-6xl grid grid-cols-1 lg:grid-cols-3 gap-6 z-10 h-full max-h-[650px]">
        {/* LEFT: MAIN CARD */}
        <div className="lg:col-span-2 bg-white/80 backdrop-blur-2xl rounded-[40px] shadow-xl border border-white/60 p-8 flex flex-col items-center justify-between relative">
          {/* Header */}
          <div className="flex justify-between w-full px-4">
            <div className="flex bg-gray-100/80 p-1 rounded-2xl">
              {(
                Object.keys(MODE_CONFIG) as Array<keyof typeof MODE_CONFIG>
              ).map((m) => (
                <button
                  key={m}
                  onClick={() => switchMode(m)}
                  disabled={isActive}
                  className={`flex items-center gap-2 px-4 py-2 rounded-xl text-sm font-bold transition-all ${
                    mode === m
                      ? "bg-white shadow-sm text-black"
                      : "text-gray-400 hover:text-gray-600"
                  } ${isActive ? "opacity-50 cursor-not-allowed" : ""}`}
                >
                  <span className={`${mode === m ? MODE_CONFIG[m].color : ""}`}>
                    {MODE_CONFIG[m].label}
                  </span>
                </button>
              ))}
            </div>
            <div className="flex gap-2">
              <button
                onClick={() => setIsSettingsOpen(true)}
                className="p-3 rounded-full bg-gray-50 hover:bg-gray-100 text-gray-500 transition-colors"
              >
                <Settings2 size={20} />
              </button>
              <button
                onClick={() => setIsZenMode(true)}
                className="p-3 rounded-full bg-gray-50 hover:bg-gray-100 text-gray-500 transition-colors"
              >
                <Maximize2 size={20} />
              </button>
            </div>
          </div>

          {/* Timer */}
          <div className="flex-1 flex items-center justify-center">
            <TimerCircle
              sizeClass="w-80 h-80"
              strokeWidth={6}
              fontSizeClass="text-7xl"
            />
          </div>

          {/* Controls */}
          <div className="flex items-center gap-8 mb-4">
            <button
              onClick={resetTimer}
              className="w-14 h-14 rounded-2xl bg-gray-50 hover:bg-gray-100 text-gray-400 flex items-center justify-center transition-colors hover:rotate-180 duration-500 shadow-sm"
            >
              <RotateCcw size={20} />
            </button>
            <button
              onClick={toggleTimer}
              className={`w-20 h-20 rounded-[2rem] flex items-center justify-center text-white shadow-xl hover:scale-105 transition-all bg-gradient-to-tr ${MODE_CONFIG[mode].gradient}`}
            >
              {isActive ? (
                <Pause size={32} fill="currentColor" />
              ) : (
                <Play size={32} fill="currentColor" className="ml-1" />
              )}
            </button>
            <div className="w-14 h-14 flex items-center justify-center">
              {timeLeft !== settings[mode] * 60 && (
                <button
                  onClick={handleStopAndSave}
                  className="w-14 h-14 rounded-2xl bg-red-50 hover:bg-red-100 text-red-500 flex items-center justify-center transition-colors shadow-sm"
                >
                  <Square size={20} fill="currentColor" />
                </button>
              )}
            </div>
          </div>
        </div>

        {/* RIGHT: STATS */}
        <div className="flex flex-col gap-6 h-full">
          {/* Progress */}
          <div className="bg-white/80 backdrop-blur-xl rounded-[30px] shadow-lg border border-white/50 p-6">
            <h3 className="text-lg font-bold text-gray-800 mb-4 flex items-center gap-2">
              <Timer size={20} className="text-indigo-500" /> Today
            </h3>
            <div className="space-y-4">
              <div className="bg-indigo-50/50 p-4 rounded-2xl border border-indigo-100">
                <div className="flex justify-between items-end">
                  <span className="text-3xl font-bold text-indigo-600">
                    {sessionsCount}
                  </span>
                  <span className="text-xs font-bold text-indigo-400 uppercase mb-1">
                    Sessions
                  </span>
                </div>
              </div>
              <div className="bg-teal-50/50 p-4 rounded-2xl border border-teal-100">
                <div className="flex justify-between items-end">
                  <span className="text-3xl font-bold text-teal-600">
                    {(totalFocusMinutes / 60).toFixed(1)}
                  </span>
                  <span className="text-xs font-bold text-teal-400 uppercase mb-1">
                    Hours
                  </span>
                </div>
              </div>
            </div>
          </div>

          {/* History */}
          <div className="flex-1 bg-white/80 backdrop-blur-xl rounded-[30px] shadow-lg border border-white/50 p-6 flex flex-col overflow-hidden">
            <h3 className="text-lg font-bold text-gray-800 mb-4 flex items-center gap-2">
              <History size={20} className="text-gray-400" /> Activity
            </h3>
            <div className="flex-1 overflow-y-auto space-y-3 pr-1 [&::-webkit-scrollbar]:hidden [-ms-overflow-style:none] [scrollbar-width:none]">
              {todaySessions && todaySessions.length > 0 ? (
                [...todaySessions].reverse().map((session) => (
                  <div
                    key={session.id}
                    className="flex items-center justify-between p-3 bg-white rounded-2xl border border-gray-50 shadow-sm"
                  >
                    <div className="flex items-center gap-3">
                      <div
                        className={`p-2 rounded-full ${
                          session.type === "work"
                            ? "bg-indigo-100 text-indigo-600"
                            : "bg-teal-100 text-teal-600"
                        }`}
                      >
                        {session.type === "work" ? (
                          <Brain size={14} />
                        ) : (
                          <Coffee size={14} />
                        )}
                      </div>
                      <div>
                        <p className="text-xs font-bold text-gray-700">
                          {session.type === "work" ? "Deep Focus" : "Break"}
                        </p>
                        <p className="text-[10px] text-gray-400 font-medium">
                          {format(new Date(session.completedAt), "h:mm a")}
                        </p>
                      </div>
                    </div>
                    <span className="text-xs font-bold text-gray-500 bg-gray-50 px-2 py-1 rounded-md">
                      {session.durationMinutes}m
                    </span>
                  </div>
                ))
              ) : (
                <div className="h-full flex flex-col items-center justify-center text-gray-300 text-sm">
                  <p>No sessions yet.</p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Settings Modal */}
      <AnimatePresence>
        {isSettingsOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-[10000] flex items-center justify-center bg-black/40 backdrop-blur-sm"
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              className="bg-white rounded-[40px] shadow-2xl w-96 p-8"
            >
              <div className="flex justify-between items-center mb-8">
                <h2 className="text-xl font-bold text-gray-800">
                  Timer Settings
                </h2>
                <button
                  onClick={() => setIsSettingsOpen(false)}
                  className="p-2 hover:bg-gray-100 rounded-full transition-colors"
                >
                  <X size={20} className="text-gray-500" />
                </button>
              </div>
              <div className="space-y-6">
                {(["work", "short", "long"] as const).map((settingMode) => (
                  <div key={settingMode}>
                    <label className="block text-xs font-bold text-gray-400 uppercase mb-2">
                      {MODE_CONFIG[settingMode].label} (min)
                    </label>
                    <input
                      type="number"
                      value={settings[settingMode]}
                      onChange={(e) =>
                        setSettings({
                          ...settings,
                          [settingMode]: Number(e.target.value),
                        })
                      }
                      className="w-full bg-gray-50 border-none rounded-2xl px-5 py-3.5 text-gray-800 font-bold focus:outline-none focus:ring-2 focus:ring-indigo-100"
                    />
                  </div>
                ))}
              </div>
              <div className="mt-8">
                <button
                  onClick={() => saveSettings(settings)}
                  className="w-full bg-black text-white py-4 rounded-2xl font-bold hover:bg-gray-900 transition-all shadow-lg hover:shadow-xl"
                >
                  Save Changes
                </button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
